*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --dark-text-color: #212121;
  --title-text-color: #757575;
  --brand-color: #007be0;
  --light-color: #fff;
  --grey-bg-color: #2f303a;
  --main-type-title: "Lobster";
  --main-type-text: "Bebas Neue";
}

body {
  color: var(--brand-color);
  font-family: var(--main-type-text);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  margin: 0;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

[type="button"], [type="submit"], a, button {
  cursor: pointer;
  border: none;
  border-color: none;
  transition-duration: .2s;
  transition-timing-function: ease-in;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4 {
  margin: 0;
}

.section {
  margin: auto;
  padding: 50px 0;
  display: block;
}

.header {
  max-width: 1600px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 12px 15px 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 14px 26px 12px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding: 18px 30px 16px;
  }
}

.logo_link {
  align-items: center;
  padding: 5px;
  display: flex;
}

.logo {
  width: 140px;
  align-items: center;
  padding-left: 18px;
  display: block;
}

.menu {
  align-items: center;
  padding: 5px 18px;
  display: flex;
}

.menu_contacts {
  font-weight: 500px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 12px;
  line-height: .7;
}

@media screen and (min-width: 768px) {
  .menu_contacts {
    margin-right: 20px;
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .menu_contacts {
    margin-right: 24px;
    font-size: 20px;
  }
}

.menu_main {
  color: inherit;
  background: none;
  padding: 0;
  display: inline-block;
}

.menu_main_icon {
  fill: var(--brand-color);
  width: 20px;
  height: 20px;
  display: block;
}

@media screen and (min-width: 768px) {
  .menu_main_icon {
    width: 26px;
    height: 26px;
  }
}

@media screen and (min-width: 1200px) {
  .menu_main_icon {
    width: 34px;
    height: 34px;
  }
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  align-items: center;
  margin-right: 0;
  padding: 0 18px 0 0;
  display: flex;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--dark-text-color);
  opacity: .8;
  z-index: 999;
  flex-direction: column;
  transition: transform .5s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  overflow-y: auto;
  transform: translateX(0);
}

.menu-container .menu-toggle {
  position: absolute;
  top: 16px;
  right: 16px;
}

.icon-close {
  fill: var(--brand-color);
  position: absolute;
  top: -20%;
  left: 20%;
}

.menu_wrapp {
  max-width: 1600px;
  gap: 40px;
  margin-left: 40px;
  padding-top: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu_wrapp {
    gap: 60px;
    margin-left: 60px;
    padding-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .menu_wrapp {
    gap: 100px;
    margin-left: 100px;
    padding-top: 100px;
  }
}

.menu_wrapp_nav, .menu_wrapp_social {
  padding: 0 8px;
}

@media screen and (min-width: 768px) {
  .menu_wrapp_nav, .menu_wrapp_social {
    padding: 0 12px;
  }
}

@media screen and (min-width: 1200px) {
  .menu_wrapp_nav, .menu_wrapp_social {
    padding: 0 18px;
  }
}

.menu_title {
  color: var(--title-text-color);
  margin: 0;
  padding: 0 0 24px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.56;
}

@media screen and (min-width: 768px) {
