    padding: 0 18px;
    font-size: 40px;
  }
}

.contact-link:hover {
  color: var(--brand-color);
}

.modal-form {
  padding: 0 18px;
}

.modal-list.list {
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .modal-list.list {
    gap: 20px;
  }
}

.modal-label, .text {
  color: var(--title-text-color);
  font-size: 10px;
  line-height: 1, 17;
  letter-spacing: .01em;
}

@media screen and (min-width: 1200px) {
  .modal-label, .text {
    font-size: 12px;
  }
}

.modal-input {
  width: 100%;
  height: 20px;
  border-bottom: 1px solid var(--dark-text-color);
  border-radius: 4px;
  padding: 4px;
  font-size: 10px;
  transition: border-bottom .25s;
}

@media screen and (min-width: 768px) {
  .modal-input {
    height: 30px;
    padding: 10px;
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .modal-input {
    height: 40px;
    padding: 16px;
    font-size: 20px;
  }
}

.modal-item:not(:last-child) {
  margin-bottom: 10px;
}

.modal-item {
  position: relative;
}

.modal-comment {
  width: 100%;
  height: 80px;
  letter-spacing: .01em;
  color: var(--title-text-color);
  border-bottom: 1px solid var(--dark-text-color);
  resize: none;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 20px;
  padding: 16px;
  font-size: 12px;
  line-height: 1.17;
  display: block;
}

@media screen and (min-width: 768px) {
  .modal-comment {
    height: 100px;
  }
}

@media screen and (min-width: 1200px) {
  .modal-comment {
    height: 130px;
  }
}

.modal-comment::-ms-input-placeholder {
  letter-spacing: .01em;
  color: var(--dark-text-color);
  font-size: 12px;
  line-height: 1.17;
}

.modal-comment::placeholder {
  letter-spacing: .01em;
  color: var(--dark-text-color);
  font-size: 12px;
  line-height: 1.17;
}

.modal-button {
  color: var(--brand-color);
  box-shadow: 0px 4px 4px var(--brand-color);
  letter-spacing: .06em;
  border-style: none;
  border: 1px solid var(--brand-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: auto auto 15px;
  padding: 4px 10px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 500;
  line-height: 1;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  animation: wiggle 2s linear infinite;
  display: block;
  transform: translateY(100px);
}

@media screen and (min-width: 480px) {
  .modal-button {
    padding: 6px 12px;
  }
}

@media screen and (min-width: 768px) {
  .modal-button {
    padding: 8px 16px;
    font-size: 17px;
  }
}

@media screen and (min-width: 1200px) {
  .modal-button {
    padding: 10px 20px;
    font-size: 20px;
  }
}

.modal-button:hover, .modal-button:focus {
  cursor: pointer;
  background-color: var(--light-color);
  box-shadow: 0px 4px 4px var(--title-text-color);
}

.hero {
