}

.hero-descr:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  text-align: left;
}

.hero-descr:-webkit-any(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: right;
}

.hero-descr:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: right;
}

@media screen and (min-width: 480px) {
  .hero-descr {
    padding: 0 10px;
  }
}

@media screen and (min-width: 768px) {
  .hero-descr {
    padding: 0 14px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-descr {
    padding: 0 18px;
  }
}

.hero-title {
  font-family: var(--main-type-title);
  color: var(--grey-bg-color);
  margin-bottom: 6px;
  animation: slideUp .8s .1s forwards;
  transform: translateY(100px);
}

@media screen and (min-width: 480px) {
  .hero-title {
    margin-bottom: 10px;
    font-size: 60px;
  }
}

@media screen and (min-width: 768px) {
  .hero-title {
    margin-bottom: 14px;
    font-size: 80px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-title {
    margin-bottom: 20px;
    font-size: 120px;
  }
}

@keyframes slideUp {
  0% {
    transform: translateX(500px)scale(.2);
  }

  100% {
    transform: translateX(0)scale(1);
  }
}

.hero-text, .hero-link {
  color: var(--grey-bg-color);
  margin: 0 0 6px;
  font-size: 10px;
  animation: slideUp .8s .1s forwards;
  transform: translateY(100px);
}

@media screen and (min-width: 480px) {
  .hero-text, .hero-link {
    margin-bottom: 10px;
    font-size: 12px;
  }
}

@media screen and (min-width: 768px) {
  .hero-text, .hero-link {
    margin-bottom: 14px;
    font-size: 17px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-text, .hero-link {
    margin-bottom: 20px;
    font-size: 20px;
  }
}

.hero-text:hover, .hero-link:hover {
  cursor: pointer;
}

.hero-btn {
  border: 1px solid var(--brand-color);
  background-color: rgba(255, 255, 255, .5);
  border-radius: 4px;
  padding: 4px 10px;
  font-size: 10px;
  animation: wiggle 2s linear infinite;
  transform: translateY(100px);
}

@media screen and (min-width: 480px) {
  .hero-btn {
    padding: 6px 12px;
    font-size: 12px;
  }
}

@media screen and (min-width: 768px) {
  .hero-btn {
    padding: 8px 16px;
    font-size: 17px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-btn {
    padding: 10px 20px;
    font-size: 20px;
  }
}

.hero-btn:hover {
  color: var(--light-color);
  background-color: var(--brand-color);
  cursor: pointer;
}

@keyframes wiggle {
  0%, 7% {
    transform: rotateZ(0);
  }

  15% {
    transform: rotateZ(-15deg);
  }

  20% {
    transform: rotateZ(10deg);
  }

  25% {
    transform: rotateZ(-10deg);
  }

  30% {
    transform: rotateZ(6deg);
  }

  35% {
    transform: rotateZ(-4deg);
  }

  40%, 100% {
    transform: rotateZ(0);
  }
}

